.header_wraper{
    /* border: 2px solid red; */
    background: url(/portfolio/static/media/b9.666e8605.jpg);
    width: 100%;
    height: 92vh;   
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; 
    display: -webkit-flex; 
    display: flex;
    /* justify-content: center; */
    -webkit-align-items: center;
            align-items: center; 
    margin-top: 50px;
}
.header_content{
    /* border: 2px solid orange; */
    margin-left: 15%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 50%;
}
.top_caption{
    color:black;
    font-size: 20px;
    font-family: 'Noto Serif', serif !important;
}
.name{
    color: black;
    font-size: 40px;
    font-family: 'Balsamiq Sans', cursive !important; 
}
.typed-text{
    color: #4d0000;
    font-size: 1.3rem;
    font-family: 'Noto Serif', serif;
    font-weight: 900;
}

/* ---------- Header Icon ----------- */
.header_icon_warper{
    margin-top: 16px;
    display: -webkit-flex;
    display: flex;
}
.header_icon{
    margin-right: 8px ;
}
.header_icon *{
    color: rebeccapurple;
}

/* ---------Fab Button Color -------------*/
.fab_button{
    background-color: #e0e0d1 !important;
    font-size: 12px !important;
}


/* --------------Header Pic wraper --------------- */

.header_pic_wraper{
    /* border: 2px solid red; */
    width: 50%;
    height: 85%;
    margin-left: 15%;
    margin-right: 15%;
}
.header_pic{
    width: 100%;
    height: 100%;
    border-radius: 10%;
}


/* ------------ Media Query-------------- */

@media(max-width: 960px){
    .header_wraper{
        width: 100% !important;
        height: 92vh;   
    }
    .name{
        font-size: 29px;
    }
    .typed-text{
        font-size: 19px;
    }
    .top_caption{
        font-size: 17px;     
    }
    .header_content{
        margin-left: 10%;
        width: 50%;
    }
    .header_pic_wraper{
        width: 50%;
        height: 70%;
        margin-left: 15%;
        margin-right: 15%;
    }
}
@media(max-width: 720px){
    .header_wraper{
        width: 100% !important;
        height: 92vh;   
    }
    .header_content{
        margin-left: 7%;
        width: 70%;
    }
    .name{
        font-size: 28px;
    }
    .typed-text{
        font-size: 18px;
    }
    .top_caption{
        font-size: 18px;     
    }
    
    .header_pic_wraper{
